@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
html {
  font-size: 62.5%;
  font-family: "Roboto", sans-serif;
}
body {
  background-color: #312e2b;
}
#root {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
}
button {
  font-family: inherit;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

.game {
  width: 90%;
  max-width: 900px;
  padding: 2rem 0;
  margin: 0 auto 0;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.game__details {
  background-color: #272522;
  width: min(100%, 450px);
  border-radius: 10px;
  margin: 2rem 0 0 0;
  padding: 2rem 0;
  color: #fff;
}
.game__details > .container {
  margin: 0 auto;
  width: 80%;
}
.game__details__section + .game__details__section {
  margin-top: 2rem;
}
.game__details h2 {
  margin-bottom: 0.6em;
  font-size: 1.7rem;
}
.moves {
  font-size: 1.6rem;
  font-weight: bold;
  overflow-y: scroll;
  height: 150px;
  background-color: #21201d;
  padding: 1rem;
  text-align: start;
}
.moves__rows {
  width: 2rem;
}
.moves__columns {
  width: 5rem;
}
.moves__columns button {
  padding: 0.2em 0.6em;
  color: #fff;
  font-size: inherit;
  font-weight: inherit;
}
.unselected-state {
  background-color: transparent;
}
.selected-state {
  background-color: #555451;
}

.square-color-btns button,
.piece-design-btns button,
.board-state-change-btns button {
  padding: 0.5em 0.8em;
  color: #fff;
  font-weight: bold;
  font-size: 1.6rem;
  margin-right: 1em;
}
.piece-design-btns button:last-child {
  margin-top: 1em;
}
.piece-design-btns button,
.board-state-change-btns button,
.game-finished-btns button {
  background-color: #32312f;
}

.game-finished-btns button {
  padding: 0.5em 0.5em;
  color: #fff;
  font-weight: bold;
  font-size: 1.6rem;
}
.game-results-btn {
  margin-right: 1em;
}
.play-again-btn {
  margin-top: 2rem;
  background-color: #7fa650 !important;
}
.square-color-btns button {
  font-size: 1.3rem;
}

.pattern-1 {
  background-color: #769656;
}
.pattern-2 {
  background-color: #b58863;
  margin-top: 1em;
}
.pattern-3 {
  background-color: #4b7399;
  margin-top: 1em;
}
.board-state-change-btns button:last-child {
  margin-top: 1em;
}
@media (min-width: 800px) {
  .game {
    flex-direction: row;
    justify-content: space-between;
    align-items: stretch;
    padding: 1rem 0;
  }
  .game__details {
    margin: 0 0 0 3rem;
    width: 40%;
  }
}

.promote-selection {
  background-color: #fff;
  position: absolute;
  border-radius: 2px;
  width: 12.5%;
  border-radius: 5px;
  display: flex;
  flex-direction: column;
}
.promote-selection__item {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: min(calc(var(--board-side) / 8), calc(var(--max-board-side) / 8));
  cursor: pointer;
}
.promote-selection__item img {
  height: calc(min(var(--board-side), var(--max-board-side)) / 8 * 0.75);
}
.promote-selection__item:last-child {
  background-color: #f1f1f1;
  height: min(calc(var(--board-side) / 16), calc(var(--max-board-side) / 16));
}

.square {
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
}
.square img {
  height: calc(min(var(--board-side), var(--max-board-side)) / 8 * 0.75);
}
.circle {
  width: 30%;
  height: 30%;
  background-color: #000;
  border-radius: 50%;
  opacity: 0.2;
}
.circle-piece {
  width: 100%;
  height: 100%;
  border: 5px solid #333;
  border-radius: 50%;
  opacity: 0.2;
  position: absolute;
}

:root {
  --board-side: 80vw;
  --max-board-side: 450px;
}
.board-wrapper {
  display: flex;
  width: 90%;
}
.board-wrapper > .container {
  margin: 0 auto;
}
.board {
  display: grid;
  grid-template-rows: repeat(8, 1fr);
  grid-template-columns: repeat(8, 1fr);
  width: min(80vw, 450px);
  width: min(var(--board-side), var(--max-board-side));
  height: min(80vw, 450px);
  height: min(var(--board-side), var(--max-board-side));
  position: relative;
  margin: 1.5rem 0 3rem;
}
.player {
  display: grid;
  grid-template-columns: min(10vw, 6rem) 1fr;
  color: #fff;
  background-color: #272522;
  border-radius: 5px;
  padding: 1rem;
}
.img-container {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
}
.player__picture {
  width: 100%;
  border-radius: 2px;
  outline: 2px solid #000;
}

.player__detail {
  margin-left: 1rem;
  display: flex;
  flex-direction: column;
}
.player-username {
  font-size: min(5vw, 2rem);
  margin-bottom: 0.5em;
}
.player-btns {
  display: flex;
  flex-wrap: wrap;
  font-size: min(3vw, 1.5rem);
}
.player-btns button {
  color: #fff;
  padding: 0.3em 0.6em;
  font-weight: bold;
  margin: 0 1rem 1rem 0;
  font-size: inherit;
}
.resign-btn {
  background-color: #e73e33;
}
.offer-draw-btn {
  background-color: #57bbdb;
}
.repetition-draw-btn {
  background-color: #f8a90b;
}
.captured-pieces {
  display: flex;
  align-items: flex-end;
  height: 27px;
}
.captured-piece-group + .captured-piece-group {
  margin-left: 0.4rem;
}
.captured-pieces img {
  height: min(5vw, 20px);
}
img + img {
  margin-left: -0.6rem;
}

.rotate-icon {
  fill: white;
}

.rows,
.columns {
  display: flex;
  align-items: center;
  justify-content: space-around;
  position: absolute;
  font-size: 1.2rem;
  color: #fff;
}
.rows {
  height: min(80vw, 450px);
  height: min(var(--board-side), var(--max-board-side));
  left: -1.5rem;
}
.columns {
  width: min(80vw, 450px);
  width: min(var(--board-side), var(--max-board-side));
  bottom: -2rem;
}
@media (min-width: 800px) {
  .board-wrapper {
    width: auto;
  }
}

.game-starting {
  background-color: #fff;
  padding: 2rem;
  position: absolute;
  border-radius: 5px;
  left: 0;
  right: 0;
  top: -1rem;
  bottom: -1rem;
  margin: 0 auto;
  width: min(var(--board-side) * 0.6, var(--max-board-side) * 0.6);
  color: #312e2b;

  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.game-starting h1 {
  font-size: min(5vw, 2rem);
  margin-bottom: 1em;
  text-align: center;
}

.field + .field {
  margin-top: min(4vw, 2rem);
}
.field label {
  font-size: min(3vw, 1.6rem);
  margin-bottom: 1em;
}
.field input {
  padding: 0.3rem;
  font-size: min(3vw, 1.6rem);
  width: 100%;
}

.start-btn {
  background-color: #7fa650;
  color: #fff;
  padding: 0.7em 1em;
  font-size: min(3.3vw, 1.5rem);
  font-weight: bold;
  margin-top: 2rem;
}

.game-finished {
  background-color: #fff;
  padding: 2rem;
  position: absolute;
  border-radius: 5px;
  left: 0;
  right: 0;
  top: -1rem;
  bottom: -1rem;
  margin: 0 auto;
  width: min(var(--board-side) * 0.6, var(--max-board-side) * 0.6);
  color: #312e2b;

  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.game-finished h1 {
  font-size: min(5vw, 2rem);
  text-align: center;
}
.game-finished h2 {
  font-size: min(3vw, 1.5rem);
  text-align: center;
}
.close-box {
  position: absolute;
  top: 0;
  right: 0;
  width: 2rem;
  height: 2rem;
  background-color: #e73e33;
  color: #fff;
  border-radius: 0 5px 0 0;
}
.play-again-result-btn {
  color: #fff;
  font-weight: bold;
  font-family: inherit;
  font-size: min(4vw, 1.6rem);
  padding: 0.5em 0.8em;
  margin-top: 4rem;
  background-color: #7fa650;
}

